@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap');
@import url("https://clean-u-i.netlify.app/component/component.css");
@import url('https://fonts.googleapis.com/css2?family=Creepster&family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap');
:root{
  --primary-color:#1e1e1e;
  --secondary-color:rgb(248, 245, 245);
  --pink-color: #fd5056;
  --dark-pink:#f50057;
  --red:rgb(246, 6, 6);
  --light-color:white;
  --lighter-white-color:#f0f8ff;
  --dark-color:rgba(0, 0, 0, 0.87);
  --shadow-color:rgb(0 0 0 / 5%);
  --card-shadow: 0px 0px 10px 4px rgb(0 0 0 / 5%);
  --bg-color:#f3f2f2;
  --shadow1:-3px -3px 5px rgba(255,255,255, 0.432),3px 3px 5px rgba(1,1,1, 0.432);
  --dropdown-bg:#ddd;
  --card-bg-color:rgba(22, 18, 18, 0.933);
  --dark-color:black;
  --dropdown-bg-color:rgb(207, 201, 201);
  --playlist-color :rgb(228, 218, 218);
  --modal-button-bg:rgba(0, 0, 0, 0.6);
  --toastify-toast-background: black;
  --toastify-text-color-info: #fff;
  
}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montshttps://github.com/Priya-Patel31/hotstream/pull/9/conflict?name=src%252FApp.css&ancestor_oid=0a835a972292bdd18f48bb29e3c736740a31dfd0&base_oid=9b1ca69f4053be93646c85a3218a5017ea45be5c&head_oid=c42a7fa6b3ba9a0b269b29f694e5ebb60b708d15errat', sans-serif;
  font-weight: 500;
}
body{
  background-color: var(--dark-color);
}

.list-style-none{
  list-style: none;
}
.flex-col{
  display: flex;
  flex-direction: column;
}
.flex-wrap{
  flex-wrap: wrap;
}
a{
  text-decoration: none;
}
.text-pink{
  color:var(--dark-pink);
}
.text-grey{
  color:var(--dark-grey);
}
.text-white{
  color:var(--secondary-color)
}
.text-red{
  color:var(--red);
}
.text-center{
  text-align: center;
}
.cursor-pointer{
  cursor: pointer;
}
ul{
  margin:0;
  padding: 0;
}
.font-md{
  font-size: 0.8rem;
}
.flex-row{
  display: flex;
}
.icon-white{
  color:var(--light-color);
}
.space-1rem{
  width:100%;
  height:1rem;
}
.space-2rem{
  width:100%;
  height:2rem;
}
.space-3rem{
  width:100%;
  height:3rem;
}
.space-4rem{
  width:100%;
  height:4rem;
}
.space-5rem{
  width:100%;
  height:5rem;
}
.mx-3{
  margin: 0 2rem;
}
.justify-center{
  justify-content: center;
}
.align-center{
  align-items:center;
}
.flex-wrap{
  flex-wrap:wrap;
}
.justify-between{
  justify-content: space-between;
}
.justify-evenly{
  justify-content: space-evenly;
}
.justify-around{
  justify-content: space-around;
}
.gap-1{
  gap:1rem;
}
.w-100{
  width:100%;
}
.inline-block{
  display: inline-block;
}
.inline{
  display: inline;
}
a:hover{
  color:var(--dark-pink);
}
.text-xxs{
  font-size:0.8rem;
}
.border-bottom{
  border-bottom:4px solid var(--secondary-color)
}